import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userState: "",
    city: "",
    street: "",
    email: undefined,
    validation: undefined,
    progressNow: '0%',
    currentStep: 1,
    steps: {
      step1: {
        userState: null,
        city: null,
        street: null
      },
      step2: {
        houseNum: null,
        floor: null,
        aptNum: null
      },
      step3: {
        placeType: null,
      },
      step4: {
        roomsNum: null,
        familiesNum: null,
        bathsNum: null,
      },
      step5: {
        hasExternalPrivateEntrance: false,
        hasSeparateElectricityMeter: false,
        hasTV: false,
        hasWasher: false,
        hasDryer: false,
        hasIron: false,
        hasKitchen: false,
        hasParking: false,
        hasBanio: false,
        hasWaterHeater: false,
        hasElevator: false,
        hasHeater: false,
        hasFood: false,
        hasPool: false,
        hasNiceView: false,
        hasGenerator: false,
        hasWifi: false,
        airConditionerType: null,
        smokingAllowed: false,
        petsAllowed: false
      },
      step6: {
        images: []
      },
      step7: {
        availableAllMonth: true,
        daysAvailable: null,
        maxStayPeriod: null,
        minStayPeriod: null,
        checkInTime: null,
        checkOutTime: null,
      },
      step8: {
        pricePerDay: null,
        longPeriodDiscount: {
          hasDiscount: false,
          longPeriodDiscountPeriod: 30,
          longPeriodDiscountDiscount: 1,
        },
      },
      step9: {
        positionLat: null,
        positionLng: null,
      },
      step10: {
        iOwnTheProperty: true,
        ownerName: null,
        ownerPhoneNumber: null,
        bankInfo: {
          mBokAccountNumber: null,
          mBokAccountName: null,
          mBokAccountBranch: null,
        },
        infoProviderInfo: {
          name: null,
          phoneNumber: null,
        },
        notes: null
      }
    },

  },
  mutations: {
    // Steps & Progress
    updateCurrentStep(state, payload) {
      state.currentStep = payload.currentStep
    },
    setProgress(state, payload) {
      state.progressNow = payload.progressNow + "%"
    },
    // Step 1
    setState(state, payload) {
      state.steps.step1.userState = payload.state
    },
    setCity(state, payload) {
      state.steps.step1.city = payload.city
    },
    setStreet(state, payload) {
      state.steps.step1.street = payload.street
    },

    // Step 2
    setHouseNum(state, payload) {
      state.steps.step2.houseNum = payload.houseNum
    },
    setFloor(state, payload) {
      state.steps.step2.floor = payload.floor
    },
    setAptNum(state, payload) {
      state.steps.step2.aptNum = payload.aptNum
    },

    // Step 3
    setPlaceType(state, payload) {
      state.steps.step3.placeType = payload.placeType
    },

    // Step 4
    setRoomsNum(state, payload) {
      state.steps.step4.roomsNum = payload.roomsNum
    },
    setFamiliesNum(state, payload) {
      state.steps.step4.familiesNum = payload.familiesNum
    },
    setBathsNum(state, payload) {
      state.steps.step4.bathsNum = payload.bathsNum
    },

    // Step 5
    setHasExternalPrivateEntrance(state, payload) {
      state.steps.step5.hasExternalPrivateEntrance = payload.hasExternalPrivateEntrance
    },
    setHasSeparateElectricityMeter(state, payload) {
      state.steps.step5.hasSeparateElectricityMeter = payload.hasSeparateElectricityMeter
    },
    setHasTV(state, payload) {
      state.steps.step5.hasTV = payload.hasTV
    },
    setHasWasher(state, payload) {
      state.steps.step5.hasWasher = payload.hasWasher
    },
    setHasDryer(state, payload) {
      state.steps.step5.hasDryer = payload.hasDryer
    },
    setHasIron(state, payload) {
      state.steps.step5.hasIron = payload.hasIron
    },
    setHasKitchen(state, payload) {
      state.steps.step5.hasKitchen = payload.hasKitchen
    },
    setHasParking(state, payload) {
      state.steps.step5.hasParking = payload.hasParking
    },
    setHasBanio(state, payload) {
      state.steps.step5.hasBanio = payload.hasBanio
    },
    setHasWaterHeater(state, payload) {
      state.steps.step5.hasWaterHeater = payload.hasWaterHeater
    },
    setHasElevator(state, payload) {
      state.steps.step5.hasElevator = payload.hasElevator
    },
    setHasHeater(state, payload) {
      state.steps.step5.hasHeater = payload.hasHeater
    },
    setHasFood(state, payload) {
      state.steps.step5.hasFood = payload.hasFood
    },
    setHasPool(state, payload) {
      state.steps.step5.hasPool = payload.hasParking
    },
    setHasNiceView(state, payload) {
      state.steps.step5.hasNiceView = payload.hasNiceView
    },
    setHasGenerator(state, payload) {
      state.steps.step5.hasGenerator = payload.hasGenerator
    },
    setHasWifi(state, payload) {
      state.steps.step5.hasWifi = payload.hasWifi
    },
    setAirConditionerType(state, payload) {
      state.steps.step5.airConditionerType = payload.airConditionerType
    },
    setSmokingAllowed(state, payload) {
      state.steps.step5.smokingAllowed = payload.smokingAllowed
    },
    setPetsAllowed(state, payload) {
      state.steps.step5.petsAllowed = payload.petsAllowed
    },

    // Step 6
    setImages(state, payload) {
      state.steps.step6.images = payload.images
    },

    // Step 7
    setAvailableAllMonth(state, payload) {
      state.steps.step7.availableAllMonth = payload.availableAllMonth
    },
    setDaysAvailabele(state, payload) {
      state.steps.step7.daysAvailable = payload.daysAvailable
    },
    setMaxStayPeriod(state, payload) {
      state.steps.step7.maxStayPeriod = payload.maxStayPeriod
    },
    setMinStayPeriod(state, payload) {
      state.steps.step7.minStayPeriod = payload.minStayPeriod
    },
    setCheckInTime(state, payload) {
      state.steps.step7.checkInTime = payload.checkInTime
    },
    setCheckOutTime(state, payload) {
      state.steps.step7.checkOutTime = payload.checkOutTime
    },

    // Step 8
    setPricePerDay(state, payload) {
      state.steps.step8.pricePerDay = payload.pricePerDay
    },
    setLongPeriodDiscount(state, payload) {
      state.steps.step8.longPeriodDiscount.hasDiscount = payload.hasDiscount
    },
    setLongPeriodDiscountPeriod(state, payload) {
      state.steps.step8.longPeriodDiscount.longPeriodDiscountPeriod = payload.longPeriodDiscountPeriod
    },
    setLongPeriodDiscountDiscount(state, payload) {
      state.steps.step8.longPeriodDiscount.longPeriodDiscountDiscount = payload.longPeriodDiscountDiscount
    },


    // Step 10
    setIOwnTheProperty(state, payload) {
      state.steps.step10.iOwnTheProperty = payload.iOwnTheProperty
    },
    setOwnerName(state, payload) {
      state.steps.step10.ownerName = payload.ownerName
    },
    setOwnerPhoneNumber(state, payload) {
      state.steps.step10.ownerPhoneNumber = payload.ownerPhoneNumber
    },
    setmBokAccountNumber(state, payload) {
      state.steps.step10.bankInfo.mBokAccountNumber = payload.mBokAccountNumber
    },
    setmBokAccountName(state, payload) {
      state.steps.step10.bankInfo.mBokAccountName = payload.mBokAccountName
    },
    setmBokAccountBranch(state, payload){
      state.steps.step10.bankInfo.mBokAccountBranch = payload.mBokAccountBranch
    },
    setInfoProviderName(state, payload) {
      state.steps.step10.infoProviderInfo.name = payload.infoProviderName
    },
    setInfoProviderPhoneNumber(state, payload) {
      state.steps.step10.infoProviderInfo.phoneNumber = payload.infoProviderPhoneNumber
    },
    setNotes(state, payload) {
      state.steps.step10.notes = payload.notes
    }
  },
  actions: {},
  modules: {}
})